.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 480px;
}

.headling {
  color: var(--text-primary-light-standart-text, #F2F2F3);
  text-align: center;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
}

.button {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
}

.register {
  display: flex;
  justify-content: center;
}

.text {
  color: var(--text-dark-grey-secondary-text, #8585AD);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 0;
}

.link {
  color: var(--interface-accent, #4C4CFF);
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  margin-top: 0;
  margin-bottom: 0;
  text-decoration: none;
}