.box {
  width: 100%;
  border-radius: 40px;
  box-sizing: border-box;
  background: var(--interface-modal-bg, #1C1C21);
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.04), 0px 0px 2px 0px rgba(0, 0, 0, 0.06), 0px 4px 8px 0px rgba(0, 0, 0, 0.04);
  text-decoration: none;
  color: inherit;
  display: block;
}

.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.box_ingredient {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ingredient{
  height: 64px;
  width: 304px;
  position: relative;
}
.img {
  border-radius: 100px;
  border: 2px solid var(--button-normal, #801AB2);
  background: var(--interface-bg, #131316);
  width: 64px;
  height: 64px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img img{
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.ingredient .img:nth-child(1){
  z-index: 6;
  top: 0;
  left: 0;
}
.ingredient .img:nth-child(2){
  z-index: 5;
  top: 0;
  left: 50px;
}
.ingredient .img:nth-child(3){
  z-index: 4;
  top: 0;
  left: 100px;
}
.ingredient .img:nth-child(4){
  z-index: 3;
  top: 0;
  left: 150px;
}
.ingredient .img:nth-child(5){
  z-index: 2;
  top: 0;
  left: 200px;
}
.ingredient .img:nth-child(6){
  z-index: 1;
  top: 0;
  left: 250px;
}
.ingredient .img:nth-child(7){
  z-index: 1;
  top: 0;
  left: 250px;
}
.ingredient .img:nth-child(8){
  z-index: 1;
  top: 0;
  left: 250px;
}
.img div{
  text-align: center;
  position: absolute;
  line-height: 64px;
  top: 32px;
  left: 32px;
  transform: translate(-50%, -50%);
  display: block;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  font-size: 20px;
}
.price{
  display: flex;
  align-items: center;
}