.box {
  margin: 0 auto;
  width: 640px;
}

.number {
  text-align: center;
  width: 100%;
}

.ingredient {
  width: 100%;
  overflow-y: scroll;
  height: 312px;
  padding-right: 16px;
  box-sizing: border-box;
}

.item {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.discription {
  display: flex;
  align-items: center;
}

.img {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 2px solid var(--button-normal, #801AB2);
  background: var(--interface-bg, #131316);
  width: 64px;
  height: 64px;
  flex-shrink: 0;
}
.img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.flex{
  display: flex;
  align-items: center;
  width: calc(100% - 200px);
}
.bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.total{
  display: flex;
  align-items: center;
}